.taskToDo,
.Done {
  display: block;
  font-size: calc(50px - 1em);
  font-weight: lighter;
}

.addTaskHere {
  min-height: 250px;
}
.commonstyle1 {
  border: none;
  height: 50px;
  border-top-left-radius: 32px;
  border-bottom-right-radius: 17px;
  border-top-right-radius: 17px;
  border-bottom-left-radius: 32px;
  text-indent: 70px;
  outline: none;
}

.styletype1 {
  width: 100%;
}
.commonstyle2 {
  position: absolute;
  height: 50px;
  width: 50px;
  border-radius: 50%;
  border: none;
}
.tick {
  outline: none;
  left: 3em;
}
.tickright {
  outline: none;
  font-size: 16px;
  right: 2em;
}
button:focus {
  outline: 0;
}
